.tintuc__gioithieu{
	width: 25%;
	padding: 0 5px;
}
.content__gioithieu{
	width: 75%;
	padding: 0 15px 0 15px;
	background: #fff;
	color: #555555;
    font-size: 15px;
	line-height: 24px;
	& article h2{
		line-height: 34px;
		font-size: 24px;
		text-transform: uppercase;
		color: #222222;
		font-weight:500;
	}
	& article > img{
		margin-top:25px;
	}
	& > article > p{
		text-align: center;
		strong{
			color: #555555;
			font-size: 15px;
			line-height: 24px;
			font-weight: 500;
		}
	}
	&__left{
		width:40%;
		float:left;
		text-align: center;
		padding-top: 50px;
	}
	&__right{
		width: 60%;
		float: right;
		line-height: 24px;
		padding-left: 15px;
		padding-right: 15px;
		text-align: justify;
		strong{
			color: #000080;
			font-size:15px;
			font-weight: 500;
		}
		ul{
			list-style-type: disc;
		}
		li{
			color: #555555;
			font-size:14px;
			a{
				color: blue;
				text-decoration: underline;
				font-size: 14px;
    			font-weight: 500;
			}
		}
	}
	&__video{
		margin-top:50px;
		iframe{
			width:100%;
			height:500px;
		}
	}
	&__more{
		a{
			color:blue;
		}
	}
	.news-newer{
		.heading{
			text-transform: uppercase;
			font-size: 20px;
			margin: 10px 0;
			color: #222222;
			line-height: 34px;
		}
		a{
			color:blue;
			font-size: 12px;
			&:hover{
				text-decoration: underline;
			}
			&:focus{
				color:red;
			}
		}
		&__item__date{
			margin: 10px 0;
			color: #555555;
			font-size: 15px;
		}
	}
}
.wrap__gioithieu{
	padding: 20px 0 0 0;
	background: #f0f0f0;
}
.like-share{
	p {
	    display: inline-block;
	}
	.like__gioithieu{
		& > span {
		    display: inline-block;
		    padding: 3px 5px;
		    background: #4267b2;
		    border-radius: 3px;
		    font-size: 11px;
		    color: #fff;
		    font-weight: 600;
		    border: 1px solid #4267b2;
		}
		i{margin-right: 5px;}
	}
	.share {
	    border-radius: 3px;
	    font-size: 11px;
	    color: #fff;
	    font-weight: 600;
	    background: #4267b2;
	    padding: 3px 5px;
	    border: 1px solid #4267b2;
	}
	.gg-plus {
	    padding: 0 5px;
	    border: 1px solid #ccc;
	    color: #ed3239;
	    line-height: 21px;
	}
}
.capnhat__gioithieu{
	margin: 10px 0;
    color: #555555;
    font-size: 15px;
}
.kinhthua__gioithieu,.thuonghieu__gioithieu{
    color: #000080;
    font-size: 16px;
    font-style: italic;
    a {
	    text-decoration: underline;
	    color: #ed3239;
	}
}